<template>
  <div>
    <div class='head'>评论回复</div>
    <el-table :data="tableData"style="width: 100%">
      <el-table-column  type="index"  width="50">
      </el-table-column>
      <el-table-column label="内容" width="300" prop="content">
      </el-table-column>
      <el-table-column label="回复者" prop="fromName" >
      </el-table-column>
      <el-table-column label="评论人" prop="toName" >
      </el-table-column>
      <el-table-column label="邮箱" prop="email" width="160">
      </el-table-column>
      <el-table-column label="发布时间" width="160" getLocalTime :formatter="dateFormat" prop="updateTime">
      </el-table-column>
      <el-table-column label="操作" align="center" width="124">
        <template slot-scope="scope">
          <el-popover ref="popover{{$index}}" class="aew" placement="top" >
            <p>删除 {{ scope.row.title }} ？</p>
            <div style="text-align: right; margin: 0">
              <el-button size="mini" type="text" @click="cancel()">取消</el-button>
              <el-button size="mini" type="danger" round @click="handleDelete(scope.$index, scope.row)">确定</el-button>
            </div>
          </el-popover>
          <el-button size="mini" type="danger" v-popover:popover{{$index}} ><i class="el-icon-delete"/></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pager">
      <el-pagination layout="prev, pager, next" :current-page="currentPage" :total=totalCount @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {
      pageParam: { fo: true },
      currentPage: 1,
      totalCount: 0,
      tableData: []
    }
  },
  created() {
    if(this.$route.query.status !== null) {
      this.pageParam.mixParam = this.$route.query.status
      this.getData()
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.pageParam.page = val-1
      this.currentPage = val
      this.getData()
    },
    handleDelete(index, row) {
      document.querySelector("#app").click()
      var _this = this
      console.log(row)
      this.$ajax.get('/api/bg/removeReply?id=' + row.replyId).then(function(response){
        _this.getData()
      }).catch(function(error){console.log(error)})
    },
    dateFormat(row, column) {  
      return this.getDateTime(row[column.property])
    },
    getData() {
      var _this = this
      this.$ajax.post('/api/bg/getReply',_this.pageParam).then(function(response){
        _this.tableData = response.data.data.content
        _this.totalCount = response.data.data.totalElements
      }).catch(function(error){console.log(error)})
    },
    cancel() {
      document.querySelector("#app").click()
    },
    acsubmit(index, row) {
      var _this = this
      this.$ajax.get('/api/bg/accessReply?id=' + row.replyId).then(function(response){
        row.replyStatus = true
      }).catch(function(error){console.log(error)})
    }
  }
}
</script>

<style lang='stylus' rle='stylesheet/stylus'>
.ibadge
  margin-top 10px
  margin-right 40px
a
  text-decoration none
  color #5CACEE
a:hover
  cursor pointer
.head
  font-size 18px
  margin-bottom 14px
.el-tag--info
  border-color #fff
.el-pagination .btn-next, .el-pagination .btn-prev, .el-pagination
  color #909399
.el-pager li.active
  color	#FFB6C1
.pager
  margin 35px
  text-align center
</style>



